<template>
  <div id="app" class="flex container h-screen w-full">
    <!-- side navigation -->
    <SideNav />

    <!-- timeline -->
    <div
      class="w-full h-full overflow-y-scroll no-scrollbar"
      v-scroll:bottom="loadMore"
    >
      <div
        class="px-5 py-3 border-b border-lighter flex items-center justify-between"
      >
        <h1 class="text-xl font-bold">Home</h1>
        <i class="far fa-star text-xl text-blue"></i>
      </div>
      <div class="px-5 py-3 border-b-8 border-lighter flex">
        <div v-if="profile" class="flex-none mr-4">
          <img
            :src="profile.avatar || 'default_profile.png'"
            class="flex-none w-12 h-12 rounded-full"
            alt="Profile avatar"
          />
        </div>
        <div class="w-full relative">
          <textarea
            v-model="tweet.text"
            placeholder="What's happening?"
            class="w-full focus:outline-none mt-3 pb-3"
          ></textarea>
          <div>
            <i class="text-lg text-blue mr-4 far fa-image"></i>
            <i class="text-lg text-blue mr-4 fas fa-film"></i>
            <i class="text-lg text-blue mr-4 far fa-chart-bar"></i>
            <i class="text-lg text-blue mr-4 far fa-smile"></i>
          </div>
          <button
            @click="addNewTweet"
            type="button"
            class="h-10 px-4 text-white font-semibold bg-blue hover:bg-darkblue rounded-full absolute bottom-0 right-0"
            :class="tweet.text ? '' : 'opacity-50 cursor-not-allowed'"
          >
            Tweet
          </button>
        </div>
      </div>

      <!-- timeline -->
      <Loader :loading="loading" />
      <div
        v-if="!loading && timeline.length === 0"
        class="flex flex-col items-center justify-center w-full pt-10 px-6"
      >
        <p class="font-bold text-lg">Welcome to Warpnet!</p>
        <p class="text-sm text-dark text-center">
          This is the best place to see what's happening in your world. Find
          some people and topics to follow now.
        </p>
        <button
          class="text-white bg-blue rounded-full font-semibold mt-4 px-4 py-2 hover:bg-darkblue"
        >
          <p class="hidden lg:block">Let's go!</p>
          <i class="fas fa-plus lg:hidden"></i>
        </button>
      </div>

      <Timeline :timeline="timeline" />
    </div>

    <!-- default right bar -->
    <DefaultRightBar />
  </div>
</template>

<script>
import SideNav from "../components/SideNav.vue";
import DefaultRightBar from "../components/DefaultRightBar.vue";
import Timeline from "../components/Timeline.vue";
import Loader from "../components/Loader.vue";
import { mapActions } from "vuex";
export default {
  name: "Home",
  components: {
    SideNav,
    DefaultRightBar,
    Timeline,
    Loader,
  },
  data() {
    return {
      tweet: {
        text: "",
        root_id: "",
      },
      loading: true,
      profile: {},
      timeline: [],
      timelineCursor: "",
    };
  },
  methods: {
    ...mapActions("warpnet", [
      "getOwnerProfile",
      "getMyTimeline",
      "createTweet",
    ]),
    async addNewTweet() {
      if (!this.tweet.text) return;
      await this.createTweet(this.tweet.text);
      this.tweet.text = "";
      const timelineData = await this.getMyTimeline(20, this.timelineCursor);
      this.timeline = timelineData.tweets || [];
      this.timelineCursor = timelineData.cursor;
    },
    async loadMore() {
      const timelineData = await this.getMyTimeline(20, this.timelineCursor);
      this.timeline = this.timeline.concat(timelineData.tweets  || []);
      this.timelineCursor = timelineData.cursor;
    },
  },
  async created() {
    try {
      if (this.timeline.length > 0) {
        this.loading = false;
      }
      this.profile = await this.getOwnerProfile() || {};
      const timelineData = await this.getMyTimeline(20, this.timelineCursor);
      this.timeline = timelineData.tweets  || [];
      this.timelineCursor = timelineData.cursor;
      this.loading = false;
    } catch (err) {
      console.error('Failed to load home page:', err);
      this.loading = false;
    }
  },
};
</script>