<template>
  <div class="w-full rounded-lg bg-lightest my-4">
    <div class="p-3">
      <p class="text-lg font-bold">Who to follow</p>
    </div>
    <div v-for="profile in profiles" :key="profile.id" class="w-full flex hover:bg-lighter p-3 border-t border-lighter">
      <img 
        @click="pushToProfilePage(profile.id)" 
        :src="profile.avatar || 'default_profile.png'" 
        class="w-12 h-12 rounded-full cursor-pointer"  
        :alt="profile.username"
      />
      <div class="hidden lg:block ml-4">
        <p @click="pushToProfilePage(profile.id)" class="text-left text-sm font-bold leading-tight cursor-pointer">{{ profile.username }}</p>
        <p class="text-left text-sm leading-tight text-dark">{{ profile.id.slice(0, 8) }}...</p>
      </div>
      <button 
        @click="follow(profile.id)" 
        class="ml-auto text-sm text-blue font-bold px-4 py-1 rounded-full border border-blue m-2"
      >
        Follow
      </button>
    </div>
    <button class="p-3 w-full hover:bg-lighter text-left text-blue border-t border-lighter">
      Show More
    </button>
  </div>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: 'WhoToFollow',
  data() {
    return {
      userProfile: undefined,
      profiles: [],
      profilesCursor: "",
    };
  },
  methods: {
    ...mapActions("warpnet", [
      "getUsers",
      "getProfile", 
      "followUser",
      "unfollowUser",
    ]),
    async pushToProfilePage(profileId) {
      this.$router.push({ 
        name: "Profile",
        params: {
          id: profileId
        }
      });
    },
    async follow(profileId) {
      try {
        await this.followUser(profileId);
      } catch (err) {
        console.error(`failed to follow [${profileId}]`, err);
      }
    },
    async unfollow(profileId) {
      try {
        await this.unfollowUser(profileId);
      } catch (err) {
        console.error(`failed to unfollow [${profileId}]`, err);
      }
    },
  },
  async created() {
    try {
      this.userProfile = await this.getProfile(this.$route.params.id);

      let profileId;
      if (this.$route.name === "Home") {
        profileId = this.userProfile.id;
      } else {
        profileId = this.$route.params.id;
      }

      const whotofollow = await this.getUsers(profileId, 20, this.profilesCursor);
      this.profiles = whotofollow.users.filter(user => user.id !== this.userProfile.id);
      this.profilesCursor = whotofollow.cursor;
    } catch (err) {
      console.error('Failed to load who to follow:', err);
      this.profiles = []
    }
  },
};
</script>